ng: antialiased;\n\t-moz-osx-font-smoothing: grayscale;\n}\n\ninput,\nbutton,\ntextarea,\nselect {\n\tfont: inherit;\n\tfont-feature-settings: inherit;\n\tfont-variation-settings: inherit;\n\tletter-spacing: inherit;\n\tcolor: inherit;\n\tborder-radius: 0;\n\tbackground-color: transparent;\n\topacity: 1;\n}\n\nbutton {\n\tcursor: pointer;\n}\n\nimg,\npicture,\nvideo,\ncanvas,\nsvg {\n\tdisplay: block;\n\tmax-inline-size: 100%;\n\tmax-block-size: 100%;\n}\n\nol,\nul,\nmenu {\n\tlist-style: none;\n\tpadding-inline: 0;\n}\n\ncode,\nkbd,\nsamp,\npre {\n\tfont-family: ui-monospace, SFMono-Regular, Consolas, 'Liberation Mono', Menlo,\n\t\tmonospace;\n\tfont-size: 1em;\n}\n\nb,\nstrong {\n\tfont-weight: bolder;\n}\n\nbutton,\n[type='button'],\n[type='reset'],\n[type='submit'] {\n\t-webkit-appearance: button;\n}\n\nsub,\nsup {\n\tfont-size: 75%;\n\tline-height: 0;\n\tposition: relative;\n\tvertical-align: baseline;\n}\nsub {\n\tbottom: -0.25em;\n}\nsup {\n\ttop: -0.5em;\n}\n\ntable {\n\tborder-color: currentcolor;\n\tborder-collapse: collapse;\n}\n\nprogress {\n\tvertical-align: baseline;\n}\n\n::-webkit-inner-spin-button,\n::-webkit-outer-spin-button {\n\theight: auto;\n}\n\n[type='search'] {\n\t-webkit-appearance: textfield;\n\toutline-offset: -2px;\n}\n\n::-webkit-search-decoration {\n\t-webkit-appearance: none;\n}\n\n::-webkit-file-upload-button {\n\t-webkit-appearance: button;\n\tfont: inherit;\n}\n\nsummary {\n\tdisplay: list-item;\n}\n/* #endregion */\n\n/* #region || VARIABLES */\n:root {\n\t--c1: #cbd5e0;\n\t--c2: #cbd5e0;\n\t--c3: #cbd5e0;\n\t--c4: #252e3d;\n\t--c5: #1a202c;\n\t--c6: #a9b2ba;\n\t--c7: #a9b2ba5e;\n\t--hdr-height: 5rem;\n\t--projects-hover-color: var(--c7);\n\t--projects-selected-color: var(--c1);\n\t--btn-radius: 7px;\n\t--btn-bgc: var(--c6);\n\t--btn-color: var(--c5);\n\t--menu-item-bgc: var(--c2);\n\t--menu-item-color: var(--c5);\n\t--search-icon-color: var(--c5);\n\t--todo-card-border: 1px solid var(--c4);\n\t--control-width: 20px;\n}\n\nhtml.dark {\n\t--c1: #1a202c;\n\t--c2: #252e3d;\n\t--c3: #2d3748;\n\t--c4: #4a5568;\n\t--c5: #cbd5e0;\n\t--c6: #a9b2ba;\n\t--c7: #4a556868;\n\t--projects-hover-color: var(--c7);\n\t--projects-selected-color: var(--c4);\n\t--btn-bgc: var(--c5);\n\t--btn-color: var(--c1);\n\t--menu-item-bgc: var(--c2);\n\t--menu-item-color: var(--c5);\n\t--search-icon-color: var(--c6);\n\t--todo-card-border: none;\n}\n\n/* #endregion */\n\n/* LAYOUT */\n.body {\n\t--aside-width: clamp(16rem, 20%, 22rem);\n\t--main-width: 1fr;\n\tdisplay: grid;\n\tgrid-template-areas:\n\t\t'aside header'\n\t\t'aside main';\n\n\tgrid-template-columns: var(--aside-width) var(--main-width);\n\tgrid-template-rows: var(--hdr-height) 1fr;\n\n\theight: 100vh;\n}\n.header {\n\tgrid-area: header;\n}\n.main {\n\tgrid-area: main;\n}\n.projects {\n\tgrid-area: aside;\n}\n\n/* GENERAL STYLES */\n.body {\n\tcolor: var(--c5);\n}\n\n.main {\n\tbackground-color: var(--c1);\n\tpadding: 1rem 2rem;\n}\n\nimg {\n\twidth: 2rem;\n}\n\ninput,\nselect {\n\tbackground-color: var(--c3);\n\tpadding: 0.3rem 0.6rem;\n\tborder-radius: var(--btn-radius);\n}\n\n.container {\n\twidth: min(100%, 90ch);\n\tmargin: 0 auto;\n}\n\n.dot-controls {\n\twidth: var(--control-width);\n\tstroke: var(--c5);\n\tfill: var(--c5);\n\tcursor: pointer;\n}\n\n.header,\n.projects {\n\tbackground-color: var(--c3);\n\tborder: 1px solid var(--c4);\n}\n\n.button {\n\tborder: none;\n\tborder-radius: var(--btn-radius);\n\tbackground-color: var(--btn-bgc);\n\tcolor: var(--btn-color);\n\tpadding: 0.5rem 1rem;\n\n\t&:hover {\n\t\tfilter: brightness(0.9);\n\t}\n\t&:active {\n\t\tfilter: brightness(0.8);\n\t}\n}\n\n/* DIALOG STYLES */\ndialog {\n\tmargin: auto auto;\n\tpadding: 2rem;\n\twidth: 70ch;\n\tborder-radius: 20px;\n\tbackground-color: var(--c1);\n\tcolor: var(--c5);\n\tborder-width: 1px;\n\tbox-shadow: 2px 2px 5px 0 var(--c2);\n\n\tlabel {\n\t\tdisplay: flex;\n\t\tflex-direction: column;\n\t}\n\n\t&::backdrop {\n\t\tbackground-color: rgba(0, 0, 0, 0.2);\n\t\tbackdr